.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.locations {
  padding-top: 0px;
  margin-top: 0;
  padding-bottom: 0px;
  background-repeat: no-repeat;
  background-image:
  linear-gradient(
      to bottom, 
      $main-bgc 5%,
      #FFF5F6 5%,
      #FFF5F6 95%,    
  );
  padding-bottom: 113px;
  @include tablet {
    padding-bottom: 158px;
    background-image: linear-gradient(
    to bottom, 
    $main-bgc 9%,
    #FFF5F6 9%,
    #FFF5F6 91%);
  }
  @include desktop {
    background-image: url(./../images/sectionbg2-1x.png),
    linear-gradient(to bottom, 
    $main-bgc 11%,
    #FFF5F6 11%,
    #FFF5F6 89%);
    background-repeat: no-repeat, no-repeat;
    padding-bottom: 165px;
    background-size: contain;
    background-position: 0 20%;

  }
}


.locations--list {
  @include tablet {
    display: flex;
    justify-content: center;
    margin: -15px;
  }
  @include desktop {
    margin: -15px;
  }
}

.locations__item {
  width: 220px;
  margin: auto;
  padding: 43px 20px;
  background: $main-bgc;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0,0.25));
  border-radius: 24px;
  @include tablet {
    flex-basis: calc((100% - 30px)/ 3);
    height: 544px;
    margin: 10px;
    padding: 50px 31px;
    margin-bottom: 0px;
  }
  @include desktop {
    flex-basis: calc((100% - 45px)/ 3);
    height: 572px;
    padding: 51px 44px;
    margin: 15px;
  }
}

@include mobile {
  .locations__item:not(:last-child) {
    margin-bottom: 20px;
  }
}

.location__name {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80px;
  height: 16px;
  margin-bottom: 25px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: $main-text;
  background-color: $coffee-bgc;
  // border: 1px solid transparent;
  border-radius: 4px;
  @include tablet {
    width: 73px;
    height: 28px;
    font-size: 16px;
    margin-bottom: 30px;
  }
}

.location__name.location__name--green {
  width: 130px;
  background: $green-bgc;
  @include tablet {
    width: 138px;
  }
}

.location__city {
  display: block;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: $second-text;
  @include tablet {
    font-size: 14px;
    margin-bottom: 14px;
  }
  @include desktop {
    font-size: 16px;
    margin-bottom: 10px;
  }
}

.location__descripion {
  margin-bottom: 25px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.15;
  color: $transparent-text;
  @include tablet {
    font-size: 14px;
    line-height: 1.66;
    margin-bottom: 30px;
  }
  @include desktop {
    font-size: 16px;
    margin-bottom: 32px;
  }
}

.location__work-wrapper {
  line-height: 1.83;
  letter-spacing: 4%;
  height: 145px;
  min-width: 179px;
  border: 1px solid #E1E1E1;
  padding-top: 25px;
  padding-bottom: 25px;
  border-left: none;
  border-right: none;
  margin-bottom: 25px;
  @include tablet {
    height: 173px;
    width: 100%;
    margin-bottom: 30px;
  }
  @include desktop {
    height: 195px;
    width: 100%;
    margin-bottom: 32px;
  }
}

.location__work {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  @include tablet {
    font-size: 14px;
    margin-bottom: 16px;
  }
  @include desktop {
    font-size: 16px;
  }
}

.location__work-days {
  color: $second-text;
}

.location__work--time {
  color: $pink-text;
}

.location__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $transparent-text;
  @include tablet {
    font-size: 14px;
  }
  @include desktop {
    font-size: 16px;
  }
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.location__buttoms {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 54px;
  @include tablet {
    flex-direction: row;
    justify-content: center;
    margin-top: 81px;
  }
  @include desktop {
    margin-top: 86px;
  }
}

.btn--dark {
  width: 162px;
  height: 44px;
  @include tablet {
    width: 192px;
    margin-right: 15px;
  }
  @include desktop {
    width: 198px;
  }
}

.btn--light {
  width: 162px;
  height: 44px;
  margin-top: 10px;
  @include tablet {
    width: 170px;
    margin-top: 0px;
  }
  @include desktop {
    width: 180px;
  }
}

.btn__name--dark {
  margin-right: 11px;
  @include tablet {
    margin-right: 13px;
  }
  @include desktop {
    margin-right: 14px;
  }
}

.btn__name--light {
  margin-right: 15px;
  @include tablet {
    margin-right: 15px;
  }
  @include desktop {
    margin-right: 17px;
  }
}

.icon__btn--dark {
  stroke: $main-text;
}

.icon__btn--light {
  stroke: $red-text;
}